import * as React from 'react';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import Accordion from '@mui/material/Accordion';
import CloudOutlinedIcon from '@mui/icons-material/CloudOutlined';
import CloudOffOutlinedIcon from '@mui/icons-material/CloudOffOutlined';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import Chip from '@mui/material/Chip';
import Stack from '@mui/material/Stack';
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';
import Backdrop from '@mui/material/Backdrop';
import CircularProgress from '@mui/material/CircularProgress';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import { DataGrid } from '@mui/x-data-grid';

const columns = [
  { field: 'itemset', headerName: 'Itemset', width: 300 },
  { field: 'support', headerName: 'Support', width: 130 },
];

export default function Apriori() {

  const defaultDataString = 'A,B,C,D\nA,B,C,F,L,M,O\nB,F,N,O\nB,K,C,P\nA,F,C,L,P,M,N'
  const [dataStringValue, setDataStringValue] = React.useState('');
  const [minimumSupportValue, setMinimumSupportValue] = React.useState('');
  const [candidateItemsets, setCandidateItemsets] = React.useState(null);
  const [datatypeValue, setDatatypeValue] = React.useState('string');
  const [backdropVisibility, setBackdropVisibility] = React.useState(false);
  const [errorDialogVisibility, setErrorDialogVisibility] = React.useState(false);
  const [errorMessage, setErrorMessage] = React.useState('');
  const [serverState, setServerState] = React.useState(false);
  const [serverURL, setServerURL] = React.useState("https://127.0.0.1:5000");
  const [serverURLInputDialogVisibility, setServerURLInputDialogVisibility] = React.useState(false);

  const openServerURLInputDialog = () => {
    setServerURLInputDialogVisibility(true);
  };

  const closeServerURLInputDialog  = () => {
    setServerURLInputDialogVisibility(false);
  };

  const controller = new AbortController()

// 3 second timeout:
const timeoutId = setTimeout(() => controller.abort(), 3000)
  
  async function checkServerStatus() {
    
    fetch(serverURL + "/online", {
    signal: controller.signal,
    method: "GET",
    mode: "cors",
  })
    .then((response) => {
      console.log(response)
      if (response.ok)
        return response.json()
      else
        throw new Error(response.status);

    }).then((response) => {
      // console.log(response)
      if (response['Status'] == 'OK') {
        setServerState(true)
      }
      else {
        setServerState(false)
      }

    }
    ).catch((error) => {
      console.log('error: ' + error);
      setServerState(false)
    });
}


  React.useEffect(() => {
    checkServerStatus()

    // const intervalId = setInterval(() => {
    //   checkServerStatus()
    // }, 7000)
    // return () => clearInterval(intervalId)
  }, [serverURL])

  const handleErrorClosing = () => {
    setErrorDialogVisibility(false);
  };

  const handleBackdropClosing = () => {
    setBackdropVisibility(false);
  };

  const handleDatatypeChange = (event) => {
    setDatatypeValue(event.target.value);
  };

  const handleDataStringChange = (event) => {
    setDataStringValue(event.target.value);
  };

  const [delimeterValue, setDelimeterValue] = React.useState(',');

  const handleDelimeterChange = (event) => {
    setDelimeterValue(event.target.value);
  };

  const handleServerURLChange = (event) => {
    setServerURL(event.target.value);
  };

  const handleMinimumSupportValueChange = (event) => {
    setMinimumSupportValue(event.target.value);
  };

  const Content = ({ index, set }) => {
    return (
      <>

        <Accordion disableGutters sx={{
          '& > :not(style)': {
            backgroundColor: "#D6E5FA",
            width: '40vw',
          }
        }}>
          <AccordionSummary

            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel1a-content"
            id="panel1a-header"
          >
            <Stack direction="row" spacing={1}>
              <Chip label={'Candidate Itemset ' + (index + 1)} color="secondary" />
              <Chip label={set.length} color="primary" />
            </Stack>
          </AccordionSummary>
          <AccordionDetails>
            <div style={{ width: '100%' }}>
              <DataGrid autoHeight m={2} pt={3}
                rows={set}
                columns={columns}
                sx={{

                  boxShadow: 2,
                  border: 2,
                  borderColor: 'primary.light',
                  '& .MuiDataGrid-cell:hover': {
                    color: 'primary.main',
                  },
                }}
              />
            </div>
          </AccordionDetails>
        </Accordion>
      </>
    )
  };

  const Lontent = ({ index, set }) => {
    console.log(index, set)
    return (
      <>

        <Accordion disableGutters sx={{
          '& > :not(style)': {
            backgroundColor: "#D6E5FA",
            width: '40vw',
          }
        }}>
          <AccordionSummary

            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel1a-content"
            id="panel1a-header"
          >
            <Stack direction="row" spacing={1}>
              <Chip label={'Frequent Itemset ' + (index + 1)} color="secondary" />
              <Chip label={set.length} color="primary" />
            </Stack>
          </AccordionSummary>
          <AccordionDetails>
            <div style={{ width: '100%' }}>
              <DataGrid autoHeight m={2} pt={3}
                rows={set}
                columns={columns}
                sx={{

                  boxShadow: 2,
                  border: 2,
                  borderColor: 'primary.light',
                  '& .MuiDataGrid-cell:hover': {
                    color: 'primary.main',
                  },
                }}
              />
            </div>
          </AccordionDetails>
        </Accordion>
      </>
    )
  };



  function submit() {
    setBackdropVisibility(true)
    let dataString = dataStringValue
    if (!dataString) {
      dataString = defaultDataString
      setDataStringValue(defaultDataString)
    }

    fetch(serverURL + "/apriori", {
      method: "POST",
      mode: "cors",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        datastring: dataString,
        delimeter: delimeterValue,
        min_sup: minimumSupportValue ? minimumSupportValue : 2,
        datatype: datatypeValue,
      }),
    })
      .then((response) => {
        if (response.ok)
          return response.json()
        else {
          console.log('Err', response)
          if (response['statusText'] == 'Forbidden') {
            setErrorMessage(' Server offline! Please try again later!')
          }
          else setErrorMessage(response['statusText'] + '! \nPlease recheck the data and input parameters!')
          setErrorDialogVisibility(true)
          handleBackdropClosing()
        }
      })
      .then((json) => {
        setCandidateItemsets(json)
        console.log(json);
        handleBackdropClosing()
      }).catch((error) => {
        console.log('Err', error)
        setErrorMessage('Please recheck the data and input parameters!')
        setErrorDialogVisibility(true)
        handleBackdropClosing()
      });
  }



  return (<>
    
    <Dialog open={serverURLInputDialogVisibility} onClose={closeServerURLInputDialog}>
        <DialogTitle>Server URL</DialogTitle>
        <DialogContent>
          <DialogContentText>
          Enter server URL
          </DialogContentText>
          <TextField
            autoFocus
            margin="dense"
            id="name"
            label="Email Address"
            type="email"
            fullWidth
            variant="standard"
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={closeServerURLInputDialog}>Cancel</Button>
          <Button onClick={closeServerURLInputDialog}>Subscribe</Button>
        </DialogActions>
    </Dialog>
    

    <Stack direction="row" spacing={2}>

      {
        serverState ? <Button variant="outlined" startIcon={<CloudOutlinedIcon />} sx={{ color: 'green', borderColor: 'green' }}>
          CONNECTED
        </Button> : <Button variant="outlined" startIcon={<CloudOffOutlinedIcon />}>
          DISCONNECTED
        </Button>
      }

      <TextField
          id="outlined-multiline-flexible"
          label="Server URL"
          multiline
          maxRows={20}
          value={serverURL}
          onChange={handleServerURLChange}
        />

    </Stack>

    <Backdrop
      sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
      open={backdropVisibility}
      onClick={handleBackdropClosing}
    >
      <CircularProgress color="inherit" />
    </Backdrop>

    <Dialog
      open={errorDialogVisibility}
      onClose={handleErrorClosing}
      aria-labelledby="alert-dialog-title"
      aria-describedby="alert-dialog-description"
    >
      <DialogTitle id="alert-dialog-title">
        {"Error"}
      </DialogTitle>
      <DialogContent>
        <DialogContentText id="alert-dialog-description">
          {'Oops! :( ' + errorMessage}
        </DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button onClick={handleErrorClosing} autoFocus>
          OK
        </Button>
      </DialogActions>
    </Dialog>
    <h1>Apriori Algorithm</h1>
    <Stack spacing={2.5}>



      <TextField
        id="outlined-multiline-flexible"
        fullWidth
        placeholder={defaultDataString}
        InputLabelProps={{ shrink: true }}
        label="Dataset"
        multiline
        maxRows={20}
        value={dataStringValue}
        onChange={handleDataStringChange}
      />

      <Stack direction="row" spacing={2.5}>
        <TextField
          id="outlined-number"
          label="Minimum support"
          type="number"
          value={minimumSupportValue}
          onChange={handleMinimumSupportValueChange}
        />

        <TextField
          id="outlined-multiline-flexible"
          label="Delimeter"
          multiline
          maxRows={20}
          value={delimeterValue}
          onChange={handleDelimeterChange}
        />

        <FormControl>
          <FormLabel id="demo-controlled-radio-buttons-group">Datatype (used for sorting)</FormLabel>
          <RadioGroup
            row
            aria-labelledby="demo-controlled-radio-buttons-group"
            name="controlled-radio-buttons-group"
            value={datatypeValue}
            onChange={handleDatatypeChange}
          >
            <FormControlLabel value="string" control={<Radio />} label="String" />
            <FormControlLabel value="number" control={<Radio />} label="Number" />
          </RadioGroup>
        </FormControl>


      </Stack>
      <Button variant="outlined" onClick={submit}>Submit</Button>
      <Stack alignItems="stretch" direction="row" spacing={2.5}>

        <Stack spacing={0.9}>
          {candidateItemsets ? candidateItemsets['CL'].map((course, index) => <Content key={index} index={index} set={course} />) : ''}
        </Stack>
        <Stack spacing={0.9}>
          {candidateItemsets ? candidateItemsets['LL'].map((course, index) => <Lontent key={index} index={index} set={course} />) : ''}
        </Stack>
      </Stack>
    </Stack>


  </>);
}
